html, body, #root{
  margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
}

h1, h3 {
  font-family: "lato", sans-serif;
  color: white;
  font-weight: 300;
}

body {
	background-color: rgba(0,0,0,.8);
	overflow: hidden;
}

input {
  /* color: white !important; */
  text-align: center;
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  font-weight: 300;
}



.flip-container {
	-webkit-perspective: 1000px;
	        perspective: 1000px;
	width: 55%;
	height: 58%;
	margin: auto;
	margin-top: 7%; 
}

/* flip the pane when hovered */
.flipping{
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
}

/* flip speed goes here */
.flipper {
	-webkit-transition: 0.6s;
	-o-transition: 0.6s;
	transition: 0.6s;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	position: relative;
	width: 100%;
	height: 100%;
	-webkit-box-shadow: 0 3px 10px rgba(0,0,0,.8), 0 3px 10px rgba(0,0,0,.8);
	        box-shadow: 0 3px 10px rgba(0,0,0,.8), 0 3px 10px rgba(0,0,0,.8);
}

/* hide back of pane during swap */
.front, .back, .playButtons {
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

/* front pane, placed above back */
.front {
	z-index: 2;
	/* for firefox 31 */
	-webkit-transform: rotateY(0deg);
	        transform: rotateY(0deg);
}

/* back, initially hidden pane */
.back {
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
}

.playButtons{
	display: none;
	visibility: hidden;
	opacity: 0;
	background-color: black;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-webkit-transition: visibility 0ms, opacity 300ms ease-in-out;
	-o-transition: visibility 0ms, opacity 300ms ease-in-out;
	transition: visibility 0ms, opacity 300ms ease-in-out;
}

.timer-view:hover .playButtons {
	visibility: visible;
	display: -ms-flexbox;
	display: flex;
	opacity: 0.9;
}

.playButtons > button > div,
.playButtons > button > div svg{
	width: 100%;
	height: 100% !important;
}



